.login {

	.message {
		margin-bottom: 0;
		overflow: hidden;
		font-size: .75rem;
		height: 2rem;
		color: $color-danger;

		&-enter-active,
		&-leave-active {
			transition: height .2s ease-out;
		}

		&-enter,
		&-leave-active {
			height: 0;
		}
	}
}



